<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process113"></a>

<p><br></p>
<p><span class="rvts17">每个母亲的噩梦（</span><span class="rvts50">Every Mother's Nightmare</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">起始坐标：</span>
<br><span class="rvts20">X:182,Y:187</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：法拉（</span><span class="rvts20">Fara</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">寻找</span><span class="rvts20">Fara</span><span class="rvts21">的女儿</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1016.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">法拉的女儿失踪了，通过对话（不要使用精灵角色与她对话），了解到她的女儿失踪数周了。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1017.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">之后再与法拉身边的老相好</span><span class="rvts20">Jeth</span><span class="rvts21">对话，他会告诉你，起始</span><span class="rvts20">Fara</span><span class="rvts21">的女儿已经死过了，是被几只虚空虫害死的。</span>
<br><span class="rvts21">再次和</span><span class="rvts20">Fara</span><span class="rvts21">对话并告诉她，她的女儿已经死后，该任务便关闭。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



